import React from "react";

const useKeyEvent = (target) =>{
  const [keyTarget,setKeyTarget] = React.useState(target);
  const [playedKey,setPlayedKey] = React.useState(null);
  const [keyDone,setKeyDone] = React.useState(false);
  
  React.useEffect(() => {
    const keyUp = (e) => {
      setPlayedKey(null);
    };
    const keyDown = (e) => {
          const key = e.key;
          if (key !== playedKey) {
              if(key === keyTarget){
                setKeyDone(true);
              }
            setPlayedKey(key);
          }
    };

    document.addEventListener("keydown", keyDown);
    document.addEventListener("keyup", keyUp);
    return () => {
      document.removeEventListener("keydown", keyDown);
      document.removeEventListener("keyup", keyUp);
    };
  }, [playedKey,keyTarget]);

  return [setKeyTarget,playedKey,keyDone];

}

export default function Test() {
  const [setKeyTarget,playedKey] = useKeyEvent("a");

  return (
    <div>
      <h3>{playedKey}</h3>
      <input type="text" onChange={(e)=>setKeyTarget(e.currentTarget.value)}/>
    </div>
  );
}

