import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import {
  Box,
  Button,
  Container,
  createStyles,
  Grid,
  LinearProgress,
  Theme,
  Typography,
  withStyles,
  WithStyles,
} from "@material-ui/core";
import * as Yup from "yup";
import { Field, Form, Formik } from "formik";
import { TextField } from "formik-material-ui";
import {
  profileRequest,
  updateProfile,
} from "../../../_actions/profile.actions";
import { DefaultProfileState } from "../../../_types/profile.types";
import { DefaultUserState } from "../../../_types/auth.types";
import { AppActions } from "../../../_types";
import { AppState } from "../../../_types";
import ImageInput from "./ImageInput";
import { LoadingCircle } from "../../Layout/Loadings";

const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "99vw",
      minHeight: "calc(100vh - 64px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 48px)",
        padding: theme.spacing(1),
      },
    },
    container: {
      display: "flex",
      flexDirection: "column",
      justifyContent: "space-between",
    },
    form: {
      margin: theme.spacing(2),
      display: "flex",
      justifyContent: "space-between",
      "& > div": {
        width: "100%",
      },
    },
  });

interface ProfilePageProps extends WithStyles<typeof useStyles> {
  auth: DefaultUserState;
}

type Props = ProfilePageProps &
  DefaultProfileState &
  ReturnType<typeof mapDispatchToProps>;

class Profile extends Component<Props> {
  componentDidMount() {
    if (!this.props.loading) this.props.getProfile();
  }
  render() {
    const { data, loading, classes } = this.props;
    const FILE_SIZE = 160 * 1024;
    if (!data) {
      return <LoadingCircle />;
    } else {
      const initData = {
        image_select: data.image_select,
        first_name: data.first_name,
        last_name: data.last_name,
        nickname: data.nickname,
        mobile: data.mobile ? data.mobile : "",
      };
      return (
        <Container maxWidth={false} className={classes.root}>
          <Grid container spacing={3}>
            <Grid
              item
              lg={12}
              md={12}
              xl={12}
              xs={12}
              className={classes.container}
            >
              <Container
                component="main"
                maxWidth="xs"
              >
                <Formik
                  initialValues={initData}
                  validationSchema={Yup.object().shape({
                    image_select: Yup.mixed().test(
                      "fileSize",
                      `File too large ,should be lower than ${
                        Math.floor(FILE_SIZE / 1000)
                      }kb`,
                      (value) =>
                        value && typeof value === "object"
                          ? value.size <= FILE_SIZE
                          : true
                    ),
                    first_name: Yup.string()
                      .max(255)
                      .required("First name is required"),
                    last_name: Yup.string()
                      .max(255)
                      .required("Last name is required"),
                    nickname: Yup.string()
                      .max(255)
                      .required("User name is required"),
                    mobile: Yup.string().matches(
                      phoneRegExp,
                      "Phone number is not valid"
                    ),
                  })}
                  onSubmit={(e: typeof initData, { resetForm }) => {
                    let val: any = e;
                    if (typeof e.image_select === "string")
                      val = { ...e, image_select: undefined };
                    this.props.updateProfile({
                      ...val,
                    });
                    resetForm({
                      values: {
                        ...e,
                        image_select: data.image_select,
                      },
                    });
                  }}
                >
                  {({ setFieldValue, initialValues, errors }) => (
                    <Form>
                      <Box mb={3}>
                        <Field
                          name="image_select"
                          component={ImageInput}
                          image={initialValues.image_select}
                          setFieldValue={setFieldValue}
                          margin="normal"
                          errorMessage={
                            errors["image_select"]
                              ? errors["image_select"]
                              : undefined
                          }
                        />
                        <Typography
                          color="textSecondary"
                          align="center"
                          variant="h5"
                        >
                          Personal Information
                        </Typography>
                        <Field
                          component={TextField}
                          color="secondary"
                          fullWidth
                          label=" "
                          margin="normal"
                          name="nickname"
                          variant="outlined"
                        />
                        <Field
                          component={TextField}
                          color="secondary"
                          fullWidth
                          label=""
                          margin="normal"
                          name="first_name"
                          variant="outlined"
                        />
                        <Field
                          component={TextField}
                          color="secondary"
                          fullWidth
                          label=" "
                          margin="normal"
                          name="last_name"
                          variant="outlined"
                        />
                        <Field
                          component={TextField}
                          color="secondary"
                          fullWidth
                          label=""
                          margin="normal"
                          name="mobile"
                          type="mobile"
                          variant="outlined"
                        />
                        {loading && <LinearProgress color="secondary" />}
                        <Box my={2}>
                          <Button
                            color="secondary"
                            disabled={loading}
                            fullWidth
                            size="large"
                            type="submit"
                            variant="contained"
                          >
                            Save
                          </Button>
                        </Box>
                      </Box>
                    </Form>
                  )}
                </Formik>
              </Container>
            </Grid>
          </Grid>
        </Container>
      );
    }
  }
}

const mapStateToProps = (
  state: AppState,
  ownProps: Props
): ProfilePageProps => {
  return {
    ...state.user,
    auth: state.auth,
    classes: ownProps.classes,
  };
};

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  getProfile: bindActionCreators(profileRequest, dispatch),
  updateProfile: bindActionCreators(updateProfile, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(Profile));
