import React from "react";
import { connect } from "react-redux";
import { ThunkDispatch } from "redux-thunk";
import { bindActionCreators } from "redux";
import { Container, Grid } from "@material-ui/core";
import { getPlayTimes } from "../../../_actions/profile.actions";
import { DefaultProfileState } from "../../../_types/profile.types";
import LatestSongs from "./LatestSongs";
import { LoadingCircle } from "../../Layout/Loadings";
import { AppActions, AppState } from "../../../_types";

interface ProfilePageProps {}

type Props = ProfilePageProps & LinkDispatchProps & DefaultProfileState;

class Dashboard extends React.Component<Props> {
  componentDidMount() {
    if (!this.props.playtime) this.props.getPlayTimes();
  }

  render() {
    const { playtime, loading } = this.props;
    if (loading) {
      return <LoadingCircle />;
    }
    if (!playtime) {
      return <div>not signed...</div>;
    } else {
      return (
        <Container maxWidth={false} style={{ paddingTop: "10px" }}>
          <Grid container spacing={3}>
            <Grid item lg={12} md={12} xl={12} xs={12}>
              <LatestSongs res={playtime} />
            </Grid>
            {/* <Grid item lg={4} md={12} xl={4} xs={12}>
              <InstrumentTraffic />
            </Grid>
            <Grid
              container
              spacing={3}
              item
              lg={8}
              md={12}
              xl={8}
              xs={12}
              style={{ margin: 0, padding: 0 }}
            > */}
            {/* <Grid item lg={6} md={6} xl={6} xs={12}>
                <TotalLevel />
              </Grid>
              <Grid item lg={6} md={6} xl={6} xs={12}>
                <Budget  />
              </Grid>
              <Grid item lg={6} md={6} xl={6} xs={12}>
                <TotalCustomers  />
              </Grid>
              <Grid item lg={6} md={6} xl={6} xs={12}>
                <TotalProfit /> */}
            {/* </Grid> */}
            {/* </Grid> */}
          </Grid>
        </Container>
      );
    }
  }
}

interface LinkDispatchProps {
  getPlayTimes: () => void;
}

const mapStateToProps = (
  state: AppState,
  ownProps: Props
): ProfilePageProps => {
  return { ...state.user };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>,
  ownProps: ProfilePageProps
): LinkDispatchProps => ({
  getPlayTimes: bindActionCreators(getPlayTimes, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
