import React, { Component } from "react";
import clsx from "clsx";

import withStyles, { WithStyles } from "@material-ui/core/styles/withStyles";
import { createStyles, Theme, Typography, Avatar } from "@material-ui/core";
import { AccountCircle, ErrorOutline } from "@material-ui/icons";

const useStyles = (theme: Theme) =>
  createStyles({
    hidden: { display: "none" },

    container: {
      margin: theme.spacing(2, 0),
      textAlign: "center",
    },
    bigAvatar: {
      margin: "auto",
      width: 128,
      height: 128,
      borderColor: theme.palette.secondary.main,
      borderStyle: "solid",
      cursor: "pointer",
    },
    primaryBack: {
      background: theme.palette.secondary.main,
    },
    whiteBack: {
      background: "white",
    },
    errorBack: { background: theme.palette.error.main },
  });

interface Props extends WithStyles<typeof useStyles> {
  errorMessage: string;
  title: string;
  field: any;
  image: string;
  setFieldValue: (name: string, file: File) => void;
}
interface State {
  file?: any;
  imagePreviewUrl: any;
}
class ImageInput extends Component<Props, State> {
  fileUpload: React.RefObject<HTMLInputElement>;
  constructor(props: Props) {
    super(props);
    this.state = {
      file: undefined,
      imagePreviewUrl: null,
    };
    this.fileUpload = React.createRef();
  }

  showFileUpload = () => {
    this.fileUpload.current?.click();
  };

  handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    let reader = new FileReader();
    const files = e.target.files;
    if (files?.length) {
      const file = files[0];
      reader.onloadend = () => {
        this.setState({
          file,
          imagePreviewUrl: reader.result,
        });
      };
      reader.readAsDataURL(file);
      this.props.setFieldValue(this.props.field.name, file);
    }
  };

  render() {
    const { errorMessage, classes, image } = this.props;
    const { name } = this.props.field;
    const { file, imagePreviewUrl } = this.state;

    const avatarStyle = clsx(
      classes.bigAvatar,
      this.state.file ? [classes.whiteBack] : [classes.primaryBack],
      { [classes.errorBack]: errorMessage }
    );

    return (
      <div className={classes.container}>
        <input
          className={classes.hidden}
          id={name}
          name={name}
          type="file"
          onChange={this.handleImageChange}
          ref={this.fileUpload}
          accept="image/png, image/jpeg"
        />
        <Avatar
          className={avatarStyle}
          onClick={this.showFileUpload}
          alt="avatar"
          src={
            errorMessage
              ? undefined
              : file
              ? imagePreviewUrl
              : image
              ? image
              : undefined
          }
        >
          {errorMessage ? (
            <ErrorOutline style={{ fontSize: 36 }} />
          ) : (
            <AccountCircle style={{ fontSize: 36 }} />
          )}
        </Avatar>
        {errorMessage ? (
          <Typography variant="caption" color="error">
            {errorMessage}
          </Typography>
        ) : null}
      </div>
    );
  }
}

export default withStyles(useStyles)(ImageInput);
